import React from 'react';
import './Footer.scss';

import IosSunny from 'react-ionicons/lib/IosSunny';
// import IosSunnyOutline from 'react-ionicons/lib/IosSunnyOutline';
import IosMoon from 'react-ionicons/lib/IosMoon';
// import IosMoonOutline from 'react-ionicons/lib/IosMoonOutline';

// import MdSunny from 'react-ionicons/lib/MdSunny';
// import MdMoon from 'react-ionicons/lib/MdMoon';

function Footer(props) {

	const langs = props.langs;

	return (
		<div className="footer">
			<div className="mb-1">	
				<span className="dark-toggle-box">
					<span
						className={props.darkState === 'inactive' ? "dark-toggle dark-active" : "dark-toggle" }
						// className= "dark-toggle"
						onClick={() => props.darkModeToggle(false)}
					>
						{/* <MdSunny/> */}
						<IosSunny/>
						
						{/* Day */}
						{/* {props.darkState === 'inactive' ? <IosSunny/> : <IosSunnyOutline/> } */}
					</span>
					<span
						className={props.darkState === 'active' ? "dark-toggle dark-active" : "dark-toggle" }
						// className= "dark-toggle"
						onClick={() => props.darkModeToggle(true)}
					>
						{/* <MdMoon/> */}
						<IosMoon/>
						
						{/* Night */}
						{/* {props.darkState === 'active' ? <IosMoon/> : <IosMoonOutline/> } */}
					</span>
				</span>
				<span className="dividing-line">|</span>
		{ langs.map((lang, index) => {
			return (
				<span className="lang-toggle-box" key={index}>
					<span
						className={lang.set === "KR" ? "lang-toggle active": "lang-toggle" }
						onClick={() => props.langToggle('KR')}
					>KR</span>
					<span
						className={lang.set === "EN" ? "lang-toggle active": "lang-toggle" }
						onClick={() => props.langToggle("EN")}
					>EN</span>
				</span>
			);
		})}
			</div>	
			<div>©<a href="https://analytics.google.com/analytics/web/?authuser=1#/report-home/a174985234w242601509p226122997" target="blank" rel="noopener noreferrer"> </a>Bae Seonghyeon (github.io)</div>
		</div>
	);
}
export default React.memo(Footer);

